@import url('https://fonts.googleapis.com/css?family=Red+Hat+Display&display=swap');

body {
  background-color: #06050f;
  color: white;
}

p {
  text-align: left;
  font-size: 20px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  font-family: 'Red Hat Display', sans-serif;
}

h2 {
  font-size: 2em;
}

.nav-bar{
  color: #ffffff;
  background-color: rgb(158, 182, 184);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 0 30em;
  height: 3em;
}

.nav-link {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  transition: 0.5s;
}

.nav-link:hover{
  background-color: rgba(125, 113, 192, 0.788);
}

.nav-link-text{
  color: #ffffff;
  text-decoration: none;
}

.portfolio-image {
  width: 25%;
  margin: 1em;
}

.about-image {
  object-fit: cover;
  object-position: 100% 0;
  min-width: 300px;
  height: 300px;
  border-radius: 50%;
  margin: 0 2em 2em 0;
  flex: 1;
}

.about-layout {
  display: flex;
  flex-direction: row;
  margin: 2em;
}

.about-text {
  display: flex;
  flex-direction: column;
  text-align: left;
  flex: 5;
}